/**
 * Основные стили
 **/

html {
  font-size: 16px;
  color: #000;
  overflow-x: hidden;
}

body {
  font-family: 'roboto_condensedregular', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  overflow-x: hidden;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}

html,
body {
  overflow-x: hidden !important;
}

html body,
html div,
html dl,
html dt,
html dd,
html ul,
html li,
html h1,
html h2,
html h3,
html h4,
html h5,
html h6,
html pre,
html code,
html form,
html fieldset,
html input,
html textarea,
html p,
html blockquote,
html th,
html td,
body body,
body div,
body dl,
body dt,
body dd,
body ul,
body li,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6,
body pre,
body code,
body form,
body fieldset,
body input,
body textarea,
body p,
body blockquote,
body th,
body td {
  margin: 0;
  padding: 0;
}

html table,
body table {
  border-collapse: collapse;
  border-spacing: 0;
}

html fieldset,
html img,
html abbr,
body fieldset,
body img,
body abbr {
  border: 0;
}

html address,
html caption,
html cite,
html code,
html dfn,
html em,
html strong,
html th,
html var,
body address,
body caption,
body cite,
body code,
body dfn,
body em,
body strong,
body th,
body var {
  font-style: normal;
  font-weight: normal;
}

html ul li,
body ul li {
  list-style: none;
}

html caption,
html th,
body caption,
body th {
