.hamburger {
  background: none;
  display: block;
  cursor: pointer;
  border: 3px solid white;
  transition: 0.3s;
  outline: none;
}

.hamburger:hover,
.hamburger:active {
  border-color: #f8c255;
  outline: none;
}

.hamburger:hover .hamburger__element,
.hamburger:active .hamburger__element {
  background-color: #f8c255;
  outline: none;
}

.hamburger:hover .hamburger__element::after,
.hamburger:hover .hamburger__element::before,
.hamburger:active .hamburger__element::after,
.hamburger:active .hamburger__element::before {
  background-color: #f8c255;
  outline: none;
}

.hamburger:focus {
  border-color: #f8c255;
}

.hamburger__element {
  width: 40px;
  height: 3px;
  background-color: white;
  display: block;
  margin: 0 auto;
  transition: 0.3s;
}

.hamburger__element::after,
.hamburger__element::before {
  content: '';
  width: 40px;
  height: 3px;
  background-color: white;
  display: block;
  margin: 0 auto;
  transition: 0.3s;
}

.hamburger__element::after {
  transform: translateY(4px);
}

.hamburger__element::before {
  transform: translateY(-7px);
}

.hamburger--active {
  position: relative;
  z-index: 1;
  border-color: white;
  outline: none;
}

.hamburger--active:hover,
.hamburger--active:active,
.hamburger--active:focus {
  border-color: white;
  outline: none;
}

.hamburger--active:hover .hamburger__element,
.hamburger--active:active .hamburger__element,
.hamburger--active:focus .hamburger__element {
  background-color: white;
  outline: none;
}

.hamburger--active:hover .hamburger__element::after,
.hamburger--active:hover .hamburger__element::before,
.hamburger--active:active .hamburger__element::after,
.hamburger--active:active .hamburger__element::before,
.hamburger--active:focus .hamburger__element::after,
.hamburger--active:focus .hamburger__element::before {
  background-color: white;
  outline: none;
}

.hamburger--active .hamburger__element {
  transform: rotate(45deg);
}

.hamburger--active .hamburger__element::after {
  position: relative;
  transform: rotate(-90deg);
  z-index: 1;
}

.hamburger--active .hamburger__element::before {
  display: none;
}

.menu {
  position: absolute;
  right: 11%;
  top: 50px;
}

.menu__hamburger {
  width: 119px;
  height: 114px;
}

.menu__list {
  background-color: #f8c255;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
  flex-wrap: wrap;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  min-width: 725px;
  padding-left: 25px;
  padding-right: 119px;
  box-sizing: border-box;
  display: flex;
  transform: translate(700px);
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.menu__list--active {
  transform: translate(0px);
  opacity: 1;
  visibility: visible;
}

.menu__link {
  position: relative;
  font-size: 16px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.8;
  text-decoration: none;
  padding: 30px;
  transition: 0.5s;
}

.menu__link:hover {
  color: black;
}

.menu__link:hover::after {
  width: 50%;
  background-color: black;
}

.menu__link::after {
  content: '';
  height: 2px;
  width: 8px;
  position: absolute;
  background-color: #fff;
  left: 30px;
  bottom: 30px;
  transition: 0.5s;
}

